@use "../utils/variables";

.datos-coanil {

   .dato { 
      margin-bottom: 2rem;
      padding-bottom: 2rem;

      &-icono {
         position: relative;
         border-radius: 50%;
         background-clip: padding-box;
         // border: 8px solid transparent;  
         background-color: variables.$white;
         margin: auto;
         padding: 2rem;
         height: 128px;
         width: 128px;
         &:before {
            content: '';
            position: absolute;
            top: 0; right: 0; bottom: 0; left: 0;
            border-radius: inherit;
            background: radial-gradient(108.18% 1145% at 0% 0%, #0655CD 30%, #009EE3 70%);
            margin: -.5rem; 
            z-index: -1;
         }

         span {
            display: flex;
            align-items: center;
            justify-content: center;
            width: 100%;
            height: 100%;
         }
      }
      &-texto {
         color: variables.$primary;
         line-height: 1.25;
         padding: 0 2rem 2rem;

         &-numero {
            font-size: 3.5rem;
            font-weight: 700;
            text-align: center;
            margin-top: 2.5rem;
         }
         &-nombre {
            font-size: 1.5rem;
            font-weight: 600;
            text-align: center;
   
            &-desc {
               color: variables.$text;
               display: block;
               font-size: 1.25rem;
               font-weight: 400;
               margin-top: 2.5rem;
            }
         }
      }
   }
   & .row > div {
      &:not(:last-child) {
         .dato-texto {
            border-bottom: 2px solid rgba(variables.$text, 0.5);
         }
      }
   }

   
   @media screen and (variables.$sm) {
      & .row > div {
         .dato-texto {
            border-bottom: none!important;
            padding: 0 2rem;
         }

         &:nth-child(odd) {
            .dato-texto {
               border-right: 2px solid rgba(variables.$text, 0.5);
            }
         }
      }

   }

   @media screen and (variables.$xl) {
      .dato {
         margin-bottom: 0;
         padding-bottom: 0;
      }

      & .row > div {
         &:not(:last-child) {
            .dato-texto {
               border-right: 2px solid rgba(variables.$text, 0.5);
            }
         }
      }
   }
}

.areas-cards-container {
   display: grid;
   grid-template-columns: repeat(auto-fit, minmax(280px, 560px));
   gap: 5rem 1.5rem;
   place-content: center;
   place-items: center;

   .card-area {
      &:nth-child(1) {
         border-radius: 0px 0px 50px 0px!important;
         
         .card-img {
            &::before {
               background-image: url(./img/areas/mask-card-area-blue.svg);
            }
            &::after {
               background-color: rgba(variables.$blue, 0.3);
            }
         }
      }
      &:nth-child(2) {
         border-radius: 0px 50px 0px 0px!important;

         .card-img {
            &::before {
               background-image: url(./img/areas/mask-card-area-green.svg);
            }
            &::after {
               background-color: rgba(variables.$green, 0.3);
            }
         }
      }
      &:nth-child(3) {
         border-radius: 0px 50px 0px 0px!important;
         
         .card-img {
            &::before {
               background-image: url(./img/areas/mask-card-area-yellow.svg);
            }
            &::after {
               background-color: rgba(variables.$yellow, 0.3);
            }
         }
      }
      &:nth-child(4) {
         border-radius: 0px 0px 50px 0px!important;
         
         .card-img {
            &::before {
               background-image: url(./img/areas/mask-card-area-red.svg);
            }
            &::after {
               background-color: rgba(variables.$red, 0.3);
            }
         }
      }
   }

   @media screen and (variables.$xl) {
      grid-template-columns: repeat(2, 1fr);
   }
}

.di-desc {
   .col-img {
      &::before {
         background-image: url(img/vectors/vector-blue-2.svg);
         left: 5%;
         height: 120%;
      }
   }
}

.alianzas-slider {
   position: relative;
   padding-left: 2rem;   
   padding-right: 2rem;

   &::before {
      content: '';
      background: linear-gradient(to right, variables.$white 5%, rgba(255, 255, 255, 0) 15%), linear-gradient(to left, variables.$white 5%, rgba(255, 255, 255, 0) 15%);
      position: absolute;
      top: 0;
      left: 0;
      height: 100%;
      width: 100%;
      pointer-events: none;
      z-index: 1;
   }

   .alianza-logo {
      height: 180px;
      width: 260px;
      margin-right: 2rem;
      img {
         height: 100%;
         width: 100%;
         object-fit: contain;
      }
   }
}

#newsletter {
   @media screen and (variables.$md) {
      p {
         font-size: 1.25rem;
      }
   }
}

.img-deco {
   position: relative;

   #instagram-feed & {
       img {
         left: 0;

         @media screen and (variables.$md) {
            left: 8rem;
         }
       }
   }

   #newsletter & {
      img {
         height: auto;
         width: 120px;
         transform: rotate(-15deg);

        @media screen and (variables.$md) {
        }
      }
  }
}